/* Basic Reset and Styling */
.App {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

/* Header Styling */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    color: white;
    background: rgb(238 230 223/var(--tw-bg-opacity)); 
}

.logo {
    display: flex;
    align-items: center;
}

.logo-image {
    height: 40px;
    margin-right: 10px;
}

.logo-text {
    font-size: 2em;
    font-weight: bold;
    background: linear-gradient(45deg, #FF6F61, #D8A0D1);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
}

.premium-button {
    background: linear-gradient(45deg, #6D83F2, #4D68D3);
    border: none;
    color: white;
    padding: 12px 24px;
    font-size: 1.1em;
    font-weight: bold;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease;
}

.premium-button:hover {
    background: linear-gradient(45deg, #4D68D3, #6D83F2);
    transform: translateY(-2px);
}

.premium-button:active {
    background: linear-gradient(45deg, #4D68D3, #6D83F2);
    transform: translateY(0);
}

/* Main Styling */
main {
    max-width: 1200px;
    margin: 20px auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

h2, h3 {
    font-weight: bold;
}

.converter-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 20px;
}

.converter-title {
    font-size: 1.5em;
    text-align: center;
    margin: 0;
}

/* Form and Input Styling */
.form-group {
    margin-bottom: 20px;
}

.textarea-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

textarea, pre {
    width: 100%;
    height: 300px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
    font-family: monospace;
    resize: vertical;
    background-color: #f5f5f5;
    color: #333;
}

label {
    font-weight: bold;
    margin-bottom: 5px;
}

.file-upload {
    display: none;
}

.submit-button {
    display: inline-block;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    background: linear-gradient(45deg, #007bff, #0056b3);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

.submit-button:hover {
    background: linear-gradient(45deg, #0056b3, #003d80);
    transform: translateY(-2px);
}

.submit-button:focus {
    outline: none;
    box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.2);
}

.submit-button:active {
    background: linear-gradient(45deg, #003d80, #00264d);
    transform: translateY(1px);
}

/* Upload Container Styling */
.upload-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px dashed #ccc;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
}

.upload-label {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.upload-icon {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
}

.upload-container:hover {
    border-color: #888;
}

/* Downloas Container Styling */
.download-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px dashed #ccc;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
}

.download-label {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.download-icon {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
}

/* Table Styling */
.info-panel_table {
    color: #000;
    background: #fff;
    border: 1px solid grey;
    font-size: medium;
    border-collapse: collapse;
    width: 100%;
}

.info-panel_table thead th,
.info-panel_table tfoot th {
    color: #000;
    background: rgba(0, 0, 0, 0.1);
}

.info-panel_table td,
.info-panel_table th {
    padding: 0.5em;
    border: 1px solid #d3d3d3;
}

/* Footer Styling */
.footer {
    background: #20232a; /* Background color for the footer */
    color: white;
    text-align: center;
    padding: 20px; /* Increased padding for better spacing */
    width: 100%;
    bottom: 0;
}

.footer p {
    margin: 5px 0; /* Margin for each paragraph in the footer */
}

.footer a {
    color: white;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline; /* Underline links on hover */
}

.social-media {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 10px; /* Space above social media icons */
}

.social-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
}

.facebook-icon svg {
    stroke: #4267B2; /* Facebook color */
}

.twitter-icon svg {
    stroke: #1DA1F2; /* Twitter color */
}

.linkedin-icon svg {
    stroke: #0077B5; /* LinkedIn color */
}


/* Utility Classes */
.w-24 {
    width: 6rem;
}

.h-24 {
    height: 6rem;
}

.w-full {
    width: 100%;
}

.center-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
